import React from 'react';

import Header from './Header.js';
import PostThumbnail from './PostThumbnail.js';

import publicUrl from 'utils/publicUrl.js';

import css from './Profile.module.css';

function Profile(props) {
  const { store } = props;
  const user = store.users.find(user => user.id === store.currentUserId);

  function getPosts() {
    return store.posts.filter(post => post.userId === store.currentUserId);
  }

  function numPosts() {
    let postslist = getPosts();
    return postslist.length;
  }

  function numFollowers() {
    let flist = store.followers.filter(flr => flr.userId === store.currentUserId);
    return flist.length;
  }

  function numFollowing() {
    let flist = store.followers.filter(flr => flr.followerId === store.currentUserId);
    return flist.length;
  }

  function getThumbs() {
    return getPosts().map(post =>
      <PostThumbnail props={post} />
    );
  }

  return (
    <div>
      <Header />
        <div className={css.profilearea}>
          <div className={css.top}>
            <img src={publicUrl(user.photo)} alt="bigFace" />
            <h2>{user.id}</h2>
          </div>
          <div className={css.introduction}>
            <p><strong>{user.name}</strong></p>
            <p>{user.bio}</p>
          </div>
        </div>
        <div className={css.attributes}>
          <div>
            <p><strong>{numPosts()}</strong></p>
            <p className={css.weak}>posts</p>
          </div>
          <div>
            <p><strong>{numFollowers()}</strong></p>
            <p className={css.weak}>followers</p>
          </div>
          <div>
            <p><strong>{numFollowing()}</strong></p>
            <p className={css.weak}>following</p>
          </div>
        </div>
      <div className={css.lower}>
        <div className={css.posts}>{getThumbs()}</div>
      </div>
    </div>
  );
}

export default Profile;